                }
                p.contactMeLinkName {
                  color: white;
                  font-size: 3vw;
                  text-align: center;
                  margin: 0;
                  padding-top: 1vw;
                }
              }
              .contactMeLink > .contactMeLinkImage-wrapper {
                @include xyCenter();
                height: 64px;
              }
              .contactMeLinkImage-wrapper > img {
                width: 8vw;
                height: auto;
                border-radius: 5px;
              }
              .contactMeLink:nth-child(2) > svg {
                color: rgba(0, 0, 0, 0.8);
                width: 8vw;
              }
              .contactMeLink:nth-child(3) > svg {
                color: rgb(41, 128, 185);
                width: 8vw;
              }
              .contactMeLink:nth-child(4) > svg {
                color: rgb(236, 173, 173);
                width: 8vw;
              }
            }
          }
          .contactMeAddress-wrapper {
            width: 100%;
            height: 40%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            .contactMeAddress {
              width: fit-content;
              margin: 0;
              padding: 10px 20px;
              position: relative;
              top: 5%;
              right: 0;
              color: rgb(200, 200, 200);
              font-size: 5vw;
              font-weight: 700;
            }
            .contactMeAddress:nth-child(2) {
              color: white;
              padding: 0;
              position: relative;
              .eMailAddress {
                padding: 10px 20px;
                &:hover {
                  cursor: pointer;
                }
              }
              .copyMessage {
                opacity: 0;
                transition: opacity 0.8s ease-out;
                width: 130px;
                font-size: 20px;
                padding: 10px 15px;
                text-align: center;
                position: absolute;
                background: rgba(0, 0, 0, 0.6);
                border-radius: 8px;
                top: -80%;
                left: -15%;
                &::after {
                  top: 100%;
                  left: 50%;
                  border: solid transparent;
                  content: "";
                  height: 0;
                  width: 0;
                  position: absolute;
                  pointer-events: none;
                  border-color: rgba(0, 0, 0, 0);
                  border-top-color: rgba(0, 0, 0, 0.6);
                  border-width: 10px;
                  margin-left: -10px;
                }
              }
            }
          }
        }
      }
    }
  }
}
